.header-color {
    background: #833ab4;
    background: -webkit-linear-gradient(to right, #833ab4, #fd1d1d, #fcb045);
    background: linear-gradient(to right, #833ab4, #fd1d1d, #fcb045);
}

.landing-grid {
    background: #833ab4;
    background: -webkit-linear-gradient(to right, #833ab4, #fd1d1d, #fcb045);
    background: linear-gradient(to right, #833ab4, #fd1d1d, #fcb045);
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.projects-grid {
    display: flex;
    flex-wrap: wrap;
}

.resume-right-col {
    background: #27221F;
    color: white;
    padding: 2em;
}

.about_background {
    background: #27221F;
    color: white;
    padding: 2em;
    height: 100vh;
}

.mdl-list__item {
    color: white !important;
}

.mdl-card__title-text {
    background: white;
    padding: 7px;
    border: 1px solid rgba(0, 0, 0, 0.54);
    border-radius: 15px;
    color: black !important;
}


.mdl-card__title {
    padding-bottom: 8px !important;
    padding-left: 8px !important;
}

.contact-body {
    margin: auto;
    position: fixed;
    width: 100%;
    height: 100%;
    justify-content: center;
    padding-top: 2em;
}

.contact-grid {
    text-align: center;
    justify-content: center;
    width: 80%;
    height: 550px;
    background: white;
    color: #000;
    margin-bottom: 20px;
}

.contact-grid h2 {
    font-family: 'Anton', sans-serif;
}

.contact-grid p {
    font-family: 'Oxygen', sans-serif;
    font-weight: bold;
}

.contact-grid hr {
    border-top: 5px dotted black;
    width: 50%;
    margin: auto;
}

.contact-list i {
    font-size: 66px;
    padding-right: 5rem;
}

.dots {
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.04em;
    line-height: 1;
    min-height: 48px;
    box-sizing: border-box;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    padding: 16px;
    cursor: default;
}

.avatar-img {
    height: 250px;
    padding-top: 5em;
}

.banner-text {
    background-color: black;
    opacity: .8;
    width: 75%;
    margin: auto;
    border-radius: 10px;
}

.banner-text h1 {
    font-size: 66px;
    font-weight: bold;
    color: #fff;
    padding-top: 25px;
}

.banner-text hr {
    border-top: 5px dotted white;
    width: 50%;
    margin: auto;
}

.banner-text p {
    color: white;
    font-size: 20px;
    padding: 1em;
    margin-top: 10px;
}

.social-links {
    display: flex;
    justify-content: space-between;
    width: 50%;
    margin: auto;
    padding-bottom: 15px;
}

.social-links i {
    color: white;
    font-size: 5em;
    padding: 8px;
}

.hr_line {
    border-top: 3px solid #833fb2;
    width: 50%;
    margin: auto;
}

.mdl-button a {
    color: #111;
    text-decoration: none;
}

.mdl-list__item {
    padding: 11px 16px;
}

.list_item_a {
    color: #111;
    text-decoration: none;
}

.list_item_a:hover {
    color: #00caea !important;
}

.about_leftside {
    float: left;
    margin-left: 10%;
}

.about_rightside {
    float: right;
    margin-right: 10%;
}

.resume_main {
    text-align: center;
}

.mdl-button--icon {
    background: #212121 !important;
    color: white !important;
}

.material-icons {
    font-size: 21px !important;
}

@media (max-width: 890px) {
    .contact-list{
        position: absolute;
    }
    .contact-list i{
        padding-right: 30px;
        font-size: 48px;
    }
    .mdl-list__item{
        display: inline!important;
    }
    .contact-body{
        position: inherit;
    }
    .social-links{
        font-size: 10px;
        width: 85%;
    }
    .banner-text p{
        font-size: 19px;
    }
    .banner-text h1 {
        font-size: 54px;
    }

    .landing-grid {
        position: inherit;
    }

    .resume_main {
        margin-top: 40px;
    }

    .about_background {
        height: 110vh;
    }

    .about_leftside {
        margin-left: 0;
    }

    .about_rightside {
        margin-right: 0;
    }

    .dots {
        padding: 14px 0;
    }

    .resume-right-col {
        width: 100% !important;
        margin: 0 !important;
    }

    .mdl-card {
        margin-top: 30px !important;
    }

    .mdl-tabs__tab-bar {
        margin-top: 8px !important;
        margin-bottom: -20px !important;
    }
}